import { useState } from 'react';
import axios from 'axios';

function StockViewer({ code }) {
  const [result, setResult] = useState({});
  const [loading, setLoading] = useState(false);

  const handleClick = async () => {
    try {
      setLoading(true);
      const response = await axios.get(process.env.REACT_APP_DOMAIN, {
        params: {
          serviceKey: process.env.REACT_APP_SERVICEKEY,
          numOfRows: 1,
          pageNo: 1,
          likeSrtnCd: code
        },
        responseType: 'text'
      });
      const parser = new DOMParser();
      const xml = parser.parseFromString(response.data, 'text/xml');
      const result = {};
      const items = xml.getElementsByTagName('item');
      for (let i = 0; i < items.length; i++) {
        const item = items[i];
        result.srtnCd = item.getElementsByTagName('srtnCd')[0].textContent;
        result.itmsNm = item.getElementsByTagName('itmsNm')[0].textContent;
        result.mrktCtg = item.getElementsByTagName('mrktCtg')[0].textContent;
        result.clpr = item.getElementsByTagName('clpr')[0].textContent;
        result.vs = item.getElementsByTagName('vs')[0].textContent;
        result.fltRt = item.getElementsByTagName('fltRt')[0].textContent;
        result.basDt = item.getElementsByTagName('basDt')[0].textContent;
        result.mkp = item.getElementsByTagName('mkp')[0].textContent;
        result.hipr = item.getElementsByTagName('hipr')[0].textContent;
        result.lopr = item.getElementsByTagName('lopr')[0].textContent;
        result.trqu = item.getElementsByTagName('trqu')[0].textContent;
      }
      setResult(result);
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }
  return (
    <div>
      <button onClick={handleClick}>Get Stock Info</button>
      <p>기준일자: {result.basDt}</p>
      <p>단축코드: {result.srtnCd}</p>
      <p>종목명: {result.itmsNm}</p>
      <p>시장구분: {result.itmsNm}</p>
      <p>종가: {result.clpr}</p>
      <p>전일대비등락: {result.vs}</p>
      <p>등락률: {result.fltRt}</p>
      <p>시가: {result.mkp}</p>
      <p>고가: {result.hipr}</p>
      <p>저가: {result.lopr}</p>
      <p>거래량: {result.trqu}</p>
    </div>
  );
}

export default StockViewer;