import { useState, useEffect } from 'react';
import Papa from 'papaparse';
import { Form, FormControl } from 'react-bootstrap';

function SearchBar(props) {
  const [csvData, setCsvData] = useState([]);
  const [searchText, setSearchText] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(-1);
  
  useEffect(() => {
    const fetchData = async () => {
      const response = await fetch('/data/stocks.csv');
      const utf8Decoder = new TextDecoder('euc-kr');
      const { data } = Papa.parse(utf8Decoder.decode(new Uint8Array(await response.arrayBuffer())), { header: true });
      setCsvData(data);
    };
    fetchData();
  }, []);

  

  const handleSearch = e => {
    const value = e.target.value;
    setSearchText(value);
    if (value.length > 0) {
      const filteredData = csvData.filter(row =>
        Object.values(row).some(val =>
          val.toLowerCase().startsWith(value.toLowerCase())
        )
      );
      setSearchResults(filteredData.slice(0, 10)); // 최대 10개까지만 보여주기
    } else {
      setSearchResults([]);
    }
  };

  const handleResultClick = row => {
    setSearchText(row.name_kor + '(' + row.code + ')');
    setSearchResults([]);
    props.onCodeChange(row.code); // 부모 컴포넌트인 App으로 코드 값을 전달
    // onSearch(row.code);
  };


  return (
   
      <div className="search-input">
        <FormControl
          type="text"
          placeholder="Search"
          value={searchText}
          onChange={handleSearch}
        />
        {searchResults.length > 0 && (
          <div className="search-results">
            {searchResults.map((result, index) => (
              <div
                key={result.code}
                className={`search-result ${
                  selectedIndex === index ? "active" : ""
                }`}
                onClick={() => handleResultClick(result)}
              >
                {result.name_kor} ({result.code})
              </div>
            ))}
          </div>
        )}
      </div>

  );
}

export default SearchBar;